      } else {
        callback();
      }
    };
    let validateSortId = (rule, value, callback) => {
      let patrn = /^\d+$/;

      if (value.trim().length === 0) {
        callback(new Error('不可为空'));
      }else if (!patrn.test(value)){
        callback(new Error('必须为正整数'));
      } else {
        callback();
      }
