        var temp = {};
        for(var i1=0;i1<arr1.length;i1++) {
          if (arr1[i1]['caption'].toString().indexOf(newVal) > -1) {
            console.info("找到内容" + newVal + "," + arr1[i1]['caption']);

            temp = arr1[i1];
            arr2.push(temp);

