        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );
      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){

          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration:3000,
            dangerouslyUseHTMLString: true,
            // iconClass:'el-icon-info',
            message: '<spand style="color:black;">'+res.data.msg + ' </spand>'
          });
        }else{
          console.info("保存失败");
          loading.close();

          this.$alert(res.data.msg, '保存失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证'){
                this.$router.push({path:'/login'});
              }
            }
          });
        }

