          that.tableDataClone = that.tableData;
        }else{
          console.info("查询失败");
          loading.close();

          that.tableData = [];
          this.$alert(res.data.msg, '查询失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证'){
                this.$router.push({path:'/login'});
              }
            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
