        isdel:that.permissionForm.isdel,
        isbtn:that.permissionForm.isbtn,
        sortId:that.permissionForm.sortId,
      };
      console.info("窗体元素："+JSON.stringify( this.permissionForm) );
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.info("valid:"+valid);
         // flag:'',//1是新增，2是修改，3是删除。
          //  layer:'',//1是同级，2是子级


             that.checkPermissionIdSuper(that.permissionForm.oldId,that.tableData,
               targetData,
             that.permissionForm.layer,
             that.permissionForm.flag);

             this.refreshTableData = new Date().getTime();

             that.permissionFormVisible1 = false;

             let platfromconfig_p_detail = '';
             let heatconfig_p_detail = '';
             let platfromconfig_b_detail = '';
             let heatconfig_b_detail = '';

             if(this.permissionType==='all'){
               if(this.tableData[0].id==='00-0'){//00-0与00-1根据情况自定义，主要区别其中一个是热力用户，另一个是平台用户
                 console.info("银行用户菜单权限:"+ JSON.stringify( this.tableData[0].detail) );
                 console.info("银行用户按钮权限:"+  JSON.stringify( this.tableData[1].detail));
                 heatconfig_p_detail = this.tableData[0].detail;
                 heatconfig_b_detail = this.tableData[1].detail;
                 platfromconfig_p_detail = this.tableData[2].detail;
                 platfromconfig_b_detail = this.tableData[3].detail;
               }
               // else if(this.tableData[0].id==='00-1'){
               //   console.info("平台用户权限:"+  JSON.stringify( this.tableData[0].detail));
               //   console.info("热力用户权限:"+  JSON.stringify( this.tableData[1].detail));
               //   heatconfig_detail = this.tableData[1].detail;
               //   platfromconfig_detail = this.tableData[0].detail;
               // }
             }else  if(this.permissionType==='heatconfig'){
               console.info("银行用户权限:"+  JSON.stringify( this.tableData));
               heatconfig_p_detail = this.tableData;
             }else  if(this.permissionType==='platfromconfig'){
               console.info("平台用户权限:"+  JSON.stringify( this.tableData));
               platfromconfig_p_detail = this.tableData;
             }

          this.postModifyPermisson(that.permissionForm.flag,that.permissionForm.layer, JSON.stringify( targetData),that.permissionForm.oldId);

        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    /***
     * 增加同级或子级
     * @param curId 当前选中的节点ID
     * @param sourceData tableData数据对象
     * @param targetData 新增加或修改的对象内容
     * @param layer 1是同级，2是子级
     * @param flag 1是新增，2是修改，3是删除,4是查询。5是将status修改成true
     *
     */

    checkPermissionIdSuper(curId,sourceData,targetData,layer,flag){
      console.info("checkPermissionIdSuper:"+sourceData)
      console.info("查询选中ID:"+curId);
      var result = false;
      for(var i1=0;i1<sourceData.length;i1++){
        console.info("当前id:"+sourceData[i1].id);
        if(flag==='1'){
          if(sourceData[i1].id===curId ){
            if(layer==='1'){//同级
              sourceData.push(targetData);
              this.tableDataClone = this.tableData;
              console.info("add find ok");
              result = true;
              break;
            }else if (layer==='2'){
              if(sourceData[i1].hasOwnProperty("detail")){
                console.info("add 存在detail,直接追加:"+JSON.stringify(sourceData[i1]))
                let detail = sourceData[i1].detail;

                detail.push(targetData);
                sourceData[i1].detail = detail;
                this.tableDataClone = this.tableData;
                result = true;
                break;
              }else{
                let detail = [];

                detail.push(targetData);
                sourceData[i1].detail = detail ;
                console.info("add 不存在detail,新创建detail:"+JSON.stringify(sourceData[i1].detail));
                this.tableDataClone = this.tableData;
                result = true;
                break;
              }
            }
            //break;
          } else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("add not find 递归查询detail")
              result = this.checkPermissionIdSuper(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("add not find");
              result = false;
            }
          }

        }//end if(flag==='1'){
        else  if(flag==='2'){
          if(sourceData[i1].id===curId ){
            sourceData[i1].caption = targetData.caption;
            sourceData[i1].vue = targetData.vue;
            sourceData[i1].url = targetData.url;
            sourceData[i1].icon = targetData.icon;
            sourceData[i1].isbtn = targetData.isbtn;
            sourceData[i1].isdel = targetData.isdel;
            sourceData[i1].sortId = targetData.sortId;
            // for(let k of Object.keys(targetData)){
            //   sourceData[i1][k] = targetData[k];
            //   console.info("修改内容：【"+k+"】"+sourceData[i1][k]);
            // }

            console.info("modify find ok");
            this.tableDataClone = this.tableData;
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("modify not find 递归查询detail")
              result = this.checkPermissionIdSuper(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("modify not find");
              result = false;
            }
          }
        }else if(flag==='3'){
          if(sourceData[i1].id===curId ){
            sourceData.splice(i1, 1);
            this.tableDataClone = this.tableData;
            console.info("del find ok");
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("del not find 递归查询detail")
              result = this.checkPermissionIdSuper(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("del not find");
              result = false;
            }
          }
        }else if(flag==='4'){
          console.info('查询表格内容');
          //if(sourceData[i1].caption.toLowerCase().includes(curId.toLowerCase())){
          console.info("query find indexOf:"+sourceData[i1].caption.toLowerCase().indexOf(curId.toLowerCase()));
          if(sourceData[i1].caption.toLowerCase().indexOf(curId.toLowerCase())>-1){
            let newdata = [];
            newdata.push(sourceData[i1]);
            this.tableDataClone = newdata;


            console.info("query find ok."+ sourceData[i1].caption);
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("query not find 递归查询detail")
              result = this.checkPermissionIdSuper(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("query not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        }else if(flag==='5'){
          console.info('查询表格status内容');
          //if(sourceData[i1].caption.toLowerCase().includes(curId.toLowerCase())){
          console.info("query find status id:"+sourceData[i1].id);
          if(sourceData[i1].id===curId){
            sourceData[i1].status = 'true';
            console.info("modify status find ok");
            this.tableDataClone = this.tableData;
            console.info("query find status ok id:"+ sourceData[i1].id);
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("query status not find 递归查询detail")
              result = this.checkPermissionIdSuper(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("query status not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        }


      }
      return result;

    },

    initLoopCheckTable(rows){//循环检测选中状态
      for (let i = 0; i < rows.length; i++) {



          console.info("initLoopCheckTable_i:"+i+",id:"+rows[i].id+",status:"+rows[i].status);
          if(rows[i].status==='false'){
            // this.tableRowIdMap.set(rows[i].id,false);
            this.tableRowIdMap.delete(rows[i].id );
            this.$refs.multipleTable.toggleRowSelection(rows[i],false)
          }else if(rows[i].status==='true'){
            this.tableRowIdMap.set(rows[i].id,true);

            this.$refs.multipleTable.toggleRowSelection(rows[i],true)
          }
          if(rows[i].detail){
              this.initLoopCheckTable(rows[i].detail);
          }

      }

    },



    tableSelectAll() {//选择全部及不选择
      this.tableCheckedKeys = !this.tableCheckedKeys;

      this.spliteTable(this.tableData, this.tableCheckedKeys);
      let table_arr =  this.$refs.multipleTable.selection;
      console.info("选择的行："+JSON.stringify(table_arr) );

      if(table_arr.length===0){
        this.tableRowIdMap.clear();
      }else{
        for (let i = 0; i < table_arr.length; i++) {
          this.tableRowIdMap.set(table_arr[i].id, true);
        }
      }

    },
    /**
     * 处理数据
     */
    spliteTable(rows, flag) {
      for (let i = 0; i < rows.length; i++) {
        console.info("spliteTable_i:"+i);
        this.$refs.multipleTable.toggleRowSelection(rows[i],flag);
        rows[i].status = flag;

        //this.tableRowIdMap.set(rows[i].id,flag===null?true:false);
        if(this.tableRowIdMap.has(rows[i].id)===false) {
          this.tableRowIdMap.set(rows[i].id, true);
        }else if(this.tableRowIdMap.get(rows[i].id)===true){
          // this.tableRowIdMap.set(rows[i].id,false);
          this.tableRowIdMap.delete(rows[i].id );
        }else {
          this.tableRowIdMap.set(rows[i].id,true);
        }

        if(rows[i].detail){
          this.spliteTable(rows[i].detail,flag);
        }

      }
      console.info("spliteTable_tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
    },
    //点击当前行checkbox事件 （select事件）
    tableSingleSelect(selection, row){
      if(this.tableRowIdMap.has(row.id)===false) {
        this.tableRowIdMap.set(row.id, true);
      }else if(this.tableRowIdMap.get(row.id)===true){
        // this.tableRowIdMap.set(row.id,false);
        this.tableRowIdMap.delete(row.id );
      }else {
        this.tableRowIdMap.set(row.id,true);
      }
     console.info("tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
      console.info("tableSingleSelect_row:"+JSON.stringify( row ) );
      if(row.detail){
        this.spliteTable(row.detail);
      }
    },
    //selection-changeg事件，
    handleSelectionChange(selection) {

      this.multipleSelection = selection;
      console.info("multipleSelection选择的行："+JSON.stringify( this.multipleSelection) );
    },
    //选择当前行
    handleSelectCurrent(row){
      console.info("handleSelectCurrent_row.id:"+row.id);
      if(row.status==='false'){
        row.status = 'true';
      }else  if(row.status==='true'){
        row.status = 'false';
      }

      if(row.detail!=undefined){
        this.spliteTable(row.detail);
      }

      this.$refs.multipleTable.toggleRowSelection(row);

      if(this.tableRowIdMap.has(row.id)===false) {
        this.tableRowIdMap.set(row.id, true);
      }else if(this.tableRowIdMap.get(row.id)===true){
        // this.tableRowIdMap.set(row.id,false);
        this.tableRowIdMap.delete(row.id );
      }else {
        this.tableRowIdMap.set(row.id,true);
      }
    },
    isSelect(row, index){//对某列控制是否可以选中或不选中
      // if(row.node){
      //   return false
      // }else{
      //   return true
      // }
      return true;
    },
    handleAddRow(index, row) {
      console.log("行号："+index+",行内容：" + JSON.stringify(row) );
      this.tableDialgTitle = '新增权限点 ['+row.caption+']';
      this.permissionForm.caption = '';
      this.permissionForm.vue = '';
      this.permissionForm.url = '';
      this.permissionForm.icon = '';
      this.permissionForm.oldId = row.id;
      this.permissionForm.id = this.customGlobal.genLogicNo("M_B");
      this.permissionForm.flag = '1';
      this.permissionForm.isbtn = row.isbtn;
      this.permissionForm.isdel = 'true';
      this.permissionForm.sortId = '0';

      this.formLogic.isbtn = row.isbtn;
      if(this.formLogic.isbtn==='true'){
        this.permissionForm.vue = '按钮';
        this.permissionForm.url = '按钮';
        this.permissionForm.icon = '按钮';
        this.permissionForm.layer = '1';
      }

      this.permissionFormVisible1 = true;
      // this.currentSelTableRow = row;

    },
    handleEditRow(index, row) {
      console.log("行号："+index+",行内容：" + JSON.stringify(row) );
      this.tableDialgTitle = '修改权限点 ['+row.caption+']';
      this.permissionForm.oldId = row.id;
      this.permissionForm.caption = row.caption;
      this.permissionForm.vue = row.vue;
      this.permissionForm.url = row.url;
      this.permissionForm.icon = row.icon;
      if(row.isbtn===undefined || row.isbtn==='true'){
        this.permissionForm.icon = '按钮';
        this.permissionForm.vue = '按钮';
        this.permissionForm.url = '按钮';
      }
      this.permissionForm.id = row.id;
      this.permissionForm.flag = '2';
      this.permissionForm.layer ='1';
      this.permissionForm.isbtn = row.isbtn;
      this.permissionForm.isdel = row.isdel;
      this.formLogic.isbtn = row.isbtn;
      if(row.hasOwnProperty("sortId")){
        this.permissionForm.sortId = row.sortId;
      }else{
        this.permissionForm.sortId = "0";
      }


      this.permissionFormVisible1 = true;
    },
    handleDeleteRow(index, row) {
      console.log("行号："+index+",行内容：" + JSON.stringify(row) );
      this.permissionForm.oldId = row.id;
      this.permissionForm.caption = row.caption;
      this.permissionForm.vue = row.vue;
      this.permissionForm.url = row.url;
      this.permissionForm.icon = row.icon;
      this.permissionForm.id = row.id;
      this.permissionForm.flag = '3';
      this.permissionForm.layer ='1';
      this.permissionForm.isbtn = row.isbtn;
      this.permissionForm.isdel = row.isdel;
      this.permissionForm.sortId = row.sortId;

      this.$confirm('请再次确认是否要删除[<span style="color:red;">'+row.caption+'</span>]该行记录?', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString:true,
        closeOnClickModal:false,
        closeOnPressEscape:false,

      }).then(() => {
        var targetData = {
          caption:this.permissionForm.caption,
          id:this.permissionForm.id,
          vue:this.permissionForm.vue,
          url:this.permissionForm.url,
          icon:this.permissionForm.icon,
          status:"false",
          isbtn:this.permissionForm.isbtn,
          isdel:this.permissionForm.isdel,
          sortId:this.permissionForm.sortId,
        };
        this.checkPermissionIdSuper(this.permissionForm.oldId,this.tableData,
          targetData,
          this.permissionForm.layer,
          this.permissionForm.flag);

        this.refreshTableData = new Date().getTime();

        this.postModifyPermisson(this.permissionForm.flag,this.permissionForm.layer, JSON.stringify( targetData),this.permissionForm.oldId);

        // this.$message({
        //   type: 'success',
        //   message: '删除成功!'
        // });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    showMessage(){
      let msg = this.customGlobal.mapToJson(this.tableRowIdMap);
      //msg = JSON.stringify( this.customGlobal.jsonToMap(msg));
      this.$message({
        type: 'info',
        showClose: true,
        duration:3000,
        dangerouslyUseHTMLString: true,
        // iconClass:'el-icon-info',
         //message: '<spand style="color:black;"> 已经选择的ID：'+JSON.stringify(this.tableRowIdMap) +"</spand>"
        message: '<spand style="color:black;"> 已经选择的ID：'+ msg +"</spand>"
      });
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postModifyUserOrgAndPermissionKey,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify({username:'u2',permissionkey:msg})})
      );
      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){

          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration:3000,
            dangerouslyUseHTMLString: true,
            // iconClass:'el-icon-info',
            message: '<spand style="color:black;"> 保存成功 </spand>'
          });
        }else{
          console.info("保存失败");
          loading.close();

          this.$alert(res.data.msg, '保存失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证'){
                this.$router.push({path:'/login'});
              }
            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        this.$alert(JSON.stringify(error.message), '保存出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        this.$alert(JSON.stringify(res.message), '保存出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });

    },
    showMessage2(){

      this.tableRowIdMap.forEach(function(value,key){
        console.log(key + ': ' + value);
      })



      // this.$message({
      //   type: 'info',
      //   showClose: true,
      //   duration:0,
      //   dangerouslyUseHTMLString: true,
      //   // iconClass:'el-icon-info',
      //   message: '<spand style="color:black;"> 已经选择的KEY：'+  +"</spand>"
      // });
    },

  }
}
</script>
